.center {
  text-align: center;
  padding: 2rem;
  background-color: rgb(153, 180, 172);
}

.order {
  width: 70%;
  padding: 1rem;
  font-size: 1.5rem;
  font-weight: bold;
  background-color: green;
  margin-top: 1rem;
}

.button {
  width: 15%;
  padding: 0.2rem;
  font-size: 1.5rem;
  font-weight: bold;
  background-color: rgb(142, 184, 142);
  margin: 1rem;
}

.img {
  width: 250px;
}

@media (min-width: 576px) {
  .order {
    width: 50%;
  }
  .button {
    width: 10%;
  }
}

@media (min-width: 768px) {
  .order {
    width: 40%;
  }
  .button {
    width: 8%;
  }
}

@media (min-width: 992px) {
  .order {
    width: 30%;
  }
  .button {
    width: 5%;
  }
}

@media (min-width: 1200px) {
  .order {
    width: 25%;
  }
  .button {
    width: 5%;
  }
}

@media (min-width: 1400px) {
  .order {
    width: 25%;
  }
  .button {
    width: 5%;
  }
}
