.center {
  text-align: center;
  padding: 2rem;
  background-color: rgb(153, 180, 172);
}

.form {
  width: 70%;
  margin: 0 auto;
}

.input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  background-color: rgb(170, 162, 151);
  font-size: 1.5rem;
}

.login {
  width: 50%;
  padding: 1rem;
  font-size: 1.5rem;
  font-weight: bold;
  background-color: green;
  margin-top: 1rem;
  margin-left: 25%;
}

@media (min-width: 576px) {
  .form {
    width: 50%;
    margin: 0 auto;
  }
}

@media (min-width: 768px) {
  .form {
    width: 40%;
    margin: 0 auto;
  }
}

@media (min-width: 992px) {
  .form {
    width: 30%;
    margin: 0 auto;
  }
}

@media (min-width: 1200px) {
  .form {
    width: 25%;
    margin: 0 auto;
  }
}

@media (min-width: 1400px) {
  .form {
    width: 25%;
    margin: 0 auto;
  }
}
