.center {
  text-align: center;
  padding: 2rem;
  background-color: rgb(153, 180, 172);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.buttons {
  width: 70%;
  padding: 1rem;
  font-size: 1.5rem;
  font-weight: bold;
  background-color: green;
  margin-top: 1rem;
}
.img {
  width: 200px;
}

@media (min-width: 576px) {
  .buttons {
    width: 50%;
  }
}

@media (min-width: 768px) {
  .buttons {
    width: 40%;
  }
}

@media (min-width: 992px) {
  .buttons {
    width: 30%;
  }
}

@media (min-width: 1200px) {
  .buttons {
    width: 25%;
  }
}

@media (min-width: 1400px) {
  .buttons {
    width: 25%;
  }
}
